import { toggleNotice } from "../mutation-types";

export default {
  namespaced: true,
  state: {
    notice: {
      status: "",
      message: "",
    },
    isNotice: false,
    setTimeoutObject: "",
  },
  mutations: {
    [toggleNotice]: (state, notice) => {
      clearTimeout(state.setTimeoutObject);
      state.isNotice = true;
      state.notice = {
        ...state.notice,
        ...notice,
      };
      state.setTimeoutObject = setTimeout(() => {
        state.isNotice = false;
      }, 4000);
    },
  },
  actions: {},
  getters: {},
};

